import React from 'react'

const Menus = (props) => {
    return (
        <ul className="no-legend">
        <li style={{ display: 'inline'}}>
          <a href="#portfolio">Portfolio</a>
        </li>
        <li style={{ display: 'inline'}}>
          <a href="#about">About</a>
        </li>
        <li style={{ display: 'inline'}}>
          <a href="#contact">Contact</a>
        </li>
      </ul>
    )
}

export default Menus



